              :class="`${i % 2 === 0 ? 'bounce-even' : 'bounce-odd'}`"
              :data-aos="`${i % 2 === 0 ? 'fade-left' : 'fade-right'}`">
              <i :class="`${hobbie.icon} fa-2x`" aria-hidden="true"></i><br>
              <small>{{ hobbie.title }}</small>
