<script type="text/javascript">
  export default {

    name: "About",

    data() {
      return {
        hobbies   : [
          { title: "Informatique", icon: "fal fa-laptop-code laptop" },
          { title: "Cinéma", icon: "fal fa-tv television" },
          { title: "Musique", icon: "fal fa-music music" },
          { title: "Apple", icon: "fab fa-apple apple" },
          { title: "Voyages", icon: "fal fa-plane plane" },
          { title: "Football", icon: "fal fa-futbol futbol" }
        ],
        paragraphs: ["Je m'appelle Sofiane Akbly. Je dispose d'un baccalauréat scientifique obtenu avec mention. Suite à ça, j'ai décidé d'étudier l'informatique afin de découvrir ce milieu très enrichissant.", "Ainsi, mes deux années de DUT m'ont offert la possibilité d'apprendre des technologies aussi différentes les unes que les autres. Enfin, à l'obtention de mon diplôme, j'ai pris la décision de poursuivre mes études pour atteindre le titre d'ingénieur."]
      }
    }
  }</script>

<style scoped>

  #home-img {
    background-image: url(/static/img/bg/home.jpg);
    height: 50%;
    background-attachment: fixed;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover
  }

  #home-img img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -2;
    height: 50%;
    width: 100%;
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    -o-filter: blur(5px);
    filter: blur(5px)
  }

  #home-img .home-img-overlay {
    border-bottom: 5px solid #fff;
