  #parcours {
    width: 75%;
    color: #444
  }

  .item-quality {
    /*color: #aaa;*/
    /*text-align: center;*/
    /*margin-bottom: 5%;*/
    /*margin-top: 5%;*/
    /*padding: 5px;*/
    height: 100px;
    word-wrap: break-word;
    /*display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center*/
  }

  .item-quality .laptop {
    color: #26619c
  }

  .item-quality .music {
    color: #bc2a8d
  }

  .item-quality .apple {
    color: #000
  }

  .item-quality .television {
    color: #27ae60
  }

  .item-quality .futbol {
    color: #e67e22
  }

  .item-quality .plane {
    color: #1e824c
  }

</style>



