<template>
  <section id="projet" class="pt-5">

    <div class="row">

      <div class="col-6 text-center no-padding size-hover ml-auto mr-auto">
        <div class="section-title bounce-odd"><h2>Projets</h2>
          <hr>
        </div>
      </div>

      <div class="row w-100 section-content bounce-even">

        <div class="col-sm-4 mr-auto ml-auto" v-for="(p, $index) in projets">

          <div class="projet-item" :data-aos="'flip-up'" :data-aos-delay="`${($index % 3) * 100}`">
            <div class="projet-item-image"><img :src="p.infos.images[0]" alt=""></div>
            <div class="projet-item-overlay d-flex justify-content-center align-items-center flex-column"
                 @click="setProject(p)">
              <div class="projet-item-overlay-content">
                <div class="projet-item-overlay-title"><h4>{{p.title}}</h4></div>
                <div class="projet-item-overlay-infos">
                  <small>Développé pour : {{p.to}}</small>
                  <div class="more-infos">
                    <i class="fal fa-search-plus fa-3x"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>

    </div>

    <more-project :project="project"/>

  </section>
</template>

<script type="text/javascript">

  import MoreProject from './modals/MoreProject'


  export default {

    components: { MoreProject },
    methods   : {
      empty(i) {
        return (($(window).width() > 767) && (this.projets.length + i) % 4 !== 0)
      },

      setProject(project) {
        this.project = project
        $('#modalMoreProject').modal(project === null ? 'hide' : 'show')
      },
    },

    data() {
      return {
        project: null,
        projets: [{
          title: "Informations",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/iut/site_web1.png"), require("../../../assets/img/projects/iut/site_web2.png"), require("../../../assets/img/projects/iut/site_web3.png")],
            descriptions: [{
              content: `Le premier semestre a été une découverte du milieu informatique. À la fin de ce semestre, nous avons réalisé un site web qui avait pour but de renseigner les étudiants et futurs étudiants sur les stages. De plus, nous listions les curriculums vitae d'étudiants de notre promotion. Cette dernière fonctionnalité était destinée aux entreprises qui recherchaient un stagiaire.`,
              link   : false
            }],
            date        : 'Décembre 2014',
            languages   : ['HTML5', 'CSS3', 'Bootstrap']
          }
        }, {
          title: "Pacman",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/pacman/pacman1.gif"), require("../../../assets/img/projects/pacman/pacman2.png"), require("../../../assets/img/projects/pacman/pacman3.png")],
            descriptions: [{
              content: `Au semestre 2, nous avons eu l'occasion de réaliser le célèbre jeu "Pacman". Ce projet consistait encore une fois à nous faire travailler en équipe. Réaliser un jeu aussi connu permet de prendre conscience que le domaine informatique est vraiment à l'origine de beaucoup de choses. La réalisation de ce projet a engendré acquisition de nouvelles connaissances en C++.`,
              link   : false
            }],
            date        : 'Juin 2015',
            languages   : ['C++', 'SMFL']
          }
        }, {
          title: "A la croisée des chemins",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/labyrinthe/labyrinthe1.jpg"), require("../../../assets/img/projects/labyrinthe/labyrinthe2.png"), require("../../../assets/img/projects/labyrinthe/labyrinthe2.png")],
            descriptions: [{
              content: `Sans doute le projet le plus compliqué mais également celui que j'ai le plus apprécié lors de ma première année de DUT. En effet, étant plus jeune je jouais souvent au jeu "Le labyrinthe" et c'est pourquoi réaliser ce même jeu de manière informatique était passionnant.`,
              link   : false
            }],
            date        : 'Juin 2015',
            languages   : ['JAVA']
          }
        }, {
          title: "Marathon web",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/marathon/marathon_web.png"), require("../../../assets/img/projects/marathon/marathon_web1.png"), require("../../../assets/img/projects/marathon/marathon_web2.png")],
            descriptions: [{
              content: `Au semestre 3, nous avons été amenés à travailler avec le département MMI afin de mettre en commun les différentes compétences que nous avions acquises. Le mélange des départements a été effectué pour mettre en œuvre un site de financement participatif. La complexité de ce projet est qu'il fallait analyser le projet, chercher des solutions et proposer un résultat tout cela en deux petits jours, d'où le terme "Marathon web".`,
              link   : false
            }],
            date        : 'Décembre 2015',
            languages   : ['PHP', 'HTML5', 'CSS3', 'Bootstrap']
          }
        }, {
          title: "Garage automobile SINOAS",
          to   : "Garage automobile SINOAS",
          infos: {
            images      : [require("../../../assets/img/projects/garage/garage1.png"), require("../../../assets/img/projects/garage/garage2.png"), require("../../../assets/img/projects/garage/garage3.png")],
            descriptions: [{
              content: `Voilà ma première réalisation pour un professionnel. En effet, une personne de mon entourage cherchait à réaliser le site web de sa future société. Dans un premier temps, je n'avais pas les compétences requises pour un tel projet puis au fur et à mesure de mon avancée scolaire j'ai réussi à lui produire un site adapté à ses besoins.`,
              link   : false
            }],
            date        : 'Janvier 2017',
            languages   : ['HTML5', 'CSS3', 'Bootstrap', 'Materialize', 'AngularJS', 'JQuery']
          }
        }, {
          title: "My phpmyadmin",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/mpma/mpma1.png"), require("../../../assets/img/projects/mpma/mpma2.png"), require("../../../assets/img/projects/mpma/mpma3.png")],
            descriptions: [{
              content: `Comme son nom l'indique, j'ai réalisé ma propre version de "phpmyadmin" et je gère, grâce à cet outil, toutes mes bases de données MySQL`,
              link   : false
            }],
            date        : 'Décembre 2016',
            languages   : ['HTML5', 'CSS3', 'PHP', 'MySQL', 'Bootstrap', 'JQuery']
          }
        }, {
          title: "Yaghilaz",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/yaghilaz/yaghilaz1.png"), require("../../../assets/img/projects/yaghilaz/yaghilaz2.png"), require("../../../assets/img/projects/yaghilaz/yaghilaz3.png")],
            descriptions: [{
              content: `Nouvelle école nouveau programme. Afin de se familiariser avec la programmation orientée objet PHP et l'architecture MVC, j'ai réalisé un site recensant des recettes de cuisine.`,
              link   : false
            }],
            date        : 'Janvier 2017',
            languages   : ['HTML5', 'CSS3', 'PHP', 'MySQL', 'Bootstrap', 'JQuery']
          }
        }, {
          title: "Mean",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/mean/mean1.png"), require("../../../assets/img/projects/mean/mean2.png"), require("../../../assets/img/projects/mean/mean3.png")],
            descriptions: [{
              content: `Le développement web est un milieu vaste. Ainsi, après avoir étudié le PHP sur un précédent projet, j'ai approfondi mes connaissances sur l'AngularJS. Ce projet a été réalisé avec la mise en place d'une API en NodeJS. Pour vraiment montrer la différence des autres projets, même la base de données est différente. Jusque-là, la seule utilisée était MySQL, mais cette fois-ci c'est MongoDB.`,
              link   : false
            }],
            date        : 'Mai 2017',
            languages   : ['HTML5', 'CSS3', 'AngularJS', 'NodeJS', 'MongoDB', 'Materialize', 'JQuery']
          }
        }, {
          title: "Wovalen",
          to   : "Moi même",
          infos: {
            images      : [require("../../../assets/img/projects/dashboard/dashboard1.png"), require("../../../assets/img/projects/dashboard/dashboard2.png"), require("../../../assets/img/projects/dashboard/dashboard3.png")],
            descriptions: [{
              content: `Afin de mettre à profit mon apprentissage sur un plan personnel, j'ai décidé de me créer un petit tableau de bord. Cette application regroupe les horaires que j'effectue sur mon temps de travail. Ou encore un planning contenant les jours de congés dont j'ai bénéficié. De plus, elle regroupe les informations dont j'ai besoin pour mon école puis enfin un outil de mail afin de communiqué plus facilement avec cette dernière.`,
              link   : false
            }],
            date        : 'Mai 2018',
            languages   : ['HTML5', 'CSS3', 'Vue.js', 'SlimPHP', 'MySQL', 'MDBootstrap', 'JQuery']
          }
        }]
      }
    }
  }</script>

<style scoped>

  #projet {
    background-image: url(/static/img/bg/projets.jpg);
    height: 100%;
    background-attachment: fixed;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover
  }

  #projet .projet-item {
    position: relative;
    height: 25vw;
    cursor: pointer;
    margin-bottom: 1.5rem
  }

  #projet .projet-item .projet-item-image img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%)
  }

  #projet .projet-item .projet-item-image,
  #projet .projet-item .projet-item-image img {
    overflow: hidden;
    height: 100%;
    width: 100%;
    transition: all .5s linear
  }

  #projet .projet-item:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-filter: inherit;
    filter: inherit
  }

  #projet .projet-item .projet-item-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: #fff;
    background-color: rgba(0, 0, 0, .7);
    height: 100%;
    width: 100%;
    padding: 0 10px
  }

  @-webkit-keyframes projetItemOverlay {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0)
    }
    to {
      -webkit-transform: scale(1);
      transform: scale(1)
    }
  }

  @keyframes projetItemOverlay {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0)
    }
    to {
      -webkit-transform: scale(1);
      transform: scale(1)
    }
  }

  .more-infos {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 5px;
    text-align: center
  }

  #projet .projet-item .projet-item-overlay .projet-item-overlay-title h4 {
    font-family: Aqua, sans-serif;
    text-align: center;
    font-size: 22px
  }

  #projet .projet-item .projet-item-overlay .projet-item-overlay-content {
    opacity: 0;
    transition: opacity 1s
  }

  #projet .projet-item:hover .projet-item-overlay .projet-item-overlay-content {
    opacity: 1
  }

  .img-project {
    height: 19vw
  }

  .img-project img {
    height: 19vw;
    width: 100%
  }

  @media (max-width: 767px) {
    #projet .projet-item {
      height: 300px !important;
    }
  }

</style>

